import api from '../services/axios.js'
import numeral from 'numeral'
import $ from 'jquery'

export default {
  name: 'payment',
  data () {
    return {
      itemname: '',
      itemID: 0,
      img: '',
      choice: [],
      netAmount: 0,
      netPayment: 0,
      cnt: 1,
      choice_ID: 0,
      choice_name: '',
      price: 0,
      unit: '',
      oldMenu: [],
      langID: 0,
      PMC: 'bt-cancel',
      payload: {},
      test_totalbill: 0,
      TPsuccess: ''
    }
  },
  methods: {
    showDetail (item, lang) {
      // console.log(item)
      this.itemname = item.name
      this.itemID = item.item_code
      this.img = item.img
      this.unit = item.unit
      var type_list = item.type_list
      for(var i = 0; i < type_list.length; i++){
        switch (lang){
          case 1 : this.choice.push({id: type_list[i].id,name: type_list[i].name, price: type_list[i].price, isActive: false})
                  break
          case 2 : this.choice.push({id: type_list[i].id,name: type_list[i].name_en, price: type_list[i].price, isActive: false})
                  break
          case 3 : this.choice.push({id: type_list[i].id,name: type_list[i].name_cn, price: type_list[i].price, isActive: false})
                  break
        }
      }
    },
    addCNT () {
      this.cnt += 1
      this.total_bill()
    },
    rmCNT () {
      if(this.cnt > 1){
        this.cnt -= 1
      }else{
        this.cnt = 1
      }
      this.total_bill()
    },
    total_bill () {
      this.netAmount = this.cnt * this.price
    },
    select_type (inD) {
      for( var r = 0; r < this.choice.length; r++){
        if(r == inD){
          this.choice[r].isActive = true
          this.price = this.choice[r].price
          this.choice_name = this.choice[r].name
          this.choice_ID = this.choice[r].id
        }else{
          this.choice[r].isActive = false
        }
      }
      this.total_bill()
    },
    send_payment () {
      $("#payment-onhand").addClass("is-active")
      this.payload = {
                      total: this.netAmount,
                      type: 'coffee',
                      sale_subs: [{
                                    line: 0,
                                    item_id: this.itemID,
                                    item_name: this.itemname,
                                    qty: this.cnt,
                                    price_id: this.choice_ID,
                                    price_name: this.choice_name,
                                    price: this.price,
                                    unit: this.unit
                                  }]
                      }
      this.test_payment()
      // var login = {companyCode: "np", appCode: "NpInventory", saleCode: '56163', password: '56163'}
      // api.sendOrderAX (this.payload, 
      // (result) => {
      //   this.$socket.sendObj({Device:"host",type:"request",command:"onhand"})
      //   console.log(result)
      // }, (response) => {
      //   console.log(JSON.stringify(response))
      // })
    },
    bill_netAmount (payment) {      
      var money_change = 0
      this.netAmount -= payment
      this.netPayment += payment
      if(this.netAmount < 0){
        money_change = this.netAmount
        this.netAmount = 0
      }else{
        money_change = this.netAmount
        this.netAmount = this.netAmount
      }
      var total_bill = this.netPayment+money_change

      if(this.netPayment!=0){
        this.PMC = 'bt-cancel disabled'
      }
      if(money_change<=0){        
        console.log("var money_change " + Math.abs(money_change) + " netPayment "+ this.netPayment)
        if(money_change!=0){
          this.TPsuccess = ' '+ Math.abs(money_change) + ''
        }else{
          this.TPsuccess = ''
        }
        setTimeout(function(){
          $('#payment-onhand').removeClass('is-active')
          $('#payment-success').addClass('is-active')
          console.log('this is billAmount '+total_bill)
          setTimeout(function(){
            this.event_sk({command: 'print', data: 'success'})
          }.bind(this),3000)
        }.bind(this),1500)
      }      
    },
    test_payment () {
      if(this.test_totalbill==0){
        this.test_totalbill = this.netAmount
      }

      var p = Math.ceil(this.test_totalbill/15)
      var r = 0;
      var test = setInterval(function() {
        if (this.netAmount<=0){
          console.log(this.netAmount)
        }
        if(r==p){
          clearInterval(test)
        }else{          
          this.event_sk({command: 'onhand', data: 15})
        }        
        r++;
      }.bind(this), 3000)
    },
    payment_sucess (data) {
      if(data=='success'){
        $('#payment-success').removeClass('is-active')
        this.$router.push('/')
        this.netAmount = 0
        this.netPayment = 0
      }
    },
    close_modal () {
      $(".modal").removeClass("is-active")
    },
    back_item () {
      this.$router.push({ name: 'items', params: { menu: this.oldMenu, langID: this.langID }})
    },
    Format_money (int) {
      return numeral(int).format('0,0')
    },
    websocket_onmessage() {
      this.$options.sockets.onmessage = (skResult) =>
        this.event_sk(JSON.parse(skResult.data))

    },
    event_sk (data) {
      switch(data.command){
          case 'onhand' : this.bill_netAmount(data.data)
                          break
          case 'print'  : this.payment_sucess(data.data)
                          break
        }
    }
  },
  mounted () {
    var items = this.$route.params.items
    this.langID = this.$route.params.langID
    this.oldMenu = this.$route.params.oldMenu
    this.websocket_onmessage()
    if (items) { 
      this.showDetail(items, this.langID)
      this.select_type(0)
    } else { 
      this.$router.push('/') 
    }
  }
}



